.nav {
  max-width: 80%;
}

.nav__list {
  @include reset;
  display: flex;
  flex-wrap: wrap;

  // font-weight: bold;
    text-transform: uppercase;

  & li {
    font-size: 1.1em;
    padding: 30px 15px;
  }

  & li:last-child {
    padding-right: 0;
  }

  & a:hover,
  a:focus {
    border-bottom: 1px solid $btn-brown;
    color: $btn-brown;
  }

  & a:active {
    opacity: 0.8;
  }
}



@media (max-width: $mobile-width) {
  .nav {
    max-width: 100%;

  }
