.header {
  // background-color: rgba(253, 252, 250, 0.7);
  // background: linear-gradient(to right, #FFF9ED 75%, #f8e5d1 75%);
  background: linear-gradient(to right, #fff1ed 75%, #f8d2d1 75%);


}
// #f8e5d1
.header__block {
 display: flex;
 flex-wrap: nowrap;
 justify-content: space-between;
}

.header__logo {
  padding: 30px 0;

  & img {
    width: 80px;
  }
}

.header__toggle {
  display: none;
}


@media (max-width: $mobile-width) {
  .header__block {
    flex-direction: column;
    width: 100%;

    position: relative;
  }

  .header__nav {
    position: absolute;
    z-index: 4;
    width: 100%;

    top: 55px;

    // background: linear-gradient(to right, #FFF9ED 75%, #f8e5d1 75%);
    background: linear-gradient(to right, #fff1ed 75%, #f8d2d1 75%);

  }

  .header__block.container {
    padding: 0;
    width: 100%;
  }

  .header__logo {
    padding: 20px 10px;

    display: flex;
    justify-content: space-between;
  }

  // .header__toggle {
  //   display: block;
  //   width: 20px;
  //   height: 22px;

  //   background-color: transparent;
  //   border: none;
  //   cursor: pointer;
  //   background-image: url("../img/i-close.svg");
  //   background-repeat: no-repeat;

  //   &:hover {
  //     opacity: 0.7;
  //   }

  //   &:active {
  //     opacity: 0.5;
  //   }
  // }

  .header--nojs .header__toggle {
    display: none;
  }


  .header__toggle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 50px;
    height: 45px;
    margin-left: auto;
    background-color: transparent;
    border: none;
    outline: none;
    cursor: pointer;

    & span {
      position: relative;
      display: inline-block;
      width: 30px;
      height: 1px;
      background-color: $title;
      transition: background 0.3s 0.5s;
    }

    &:hover {
      opacity: 0.7;
    }

    &:active {
      opacity: 0.5;
    }
  }

  .header__toggle > span::after,
  .header__toggle > span::before {
    content: "";
    position: absolute;
    left: 0;
    width: 100%;
    height: 1px;
    background: $title;
    transform-origin: 50% 50%;
    transition: top 0.3s 0.3s, transform 0.3s;
  }

  .header__toggle > span::after {
    top: 10px;
  }

  .header__toggle > span::before {
    top: -10px;
  }

  .header--close .header__toggle > span::before {
    transform: rotate3d(0, 0, 1, 45deg);
    top: 0;
    transition: top 0.3s, transform 0.2s 0.3s;
  }

  .header--close .header__toggle > span::after {
    transform: rotate3d(0, 0, 1, -45deg);
    top: 0;
    transition: top 0.3s, transform 0.2s 0.3s;
  }

  .header--close .header__toggle > span {
    background: 0 0;
    transition: background 0.1s 0.1s;
  }

  .header .header__nav {
    display: none;
  }

  .header--nojs .header__nav {
    display: block;
  }

  .header--close .header__nav {
    display: block;
  }
}
