@mixin reset {
  margin: 0;
  padding: 0;

  list-style: none;
}

@mixin title {
  font-family: $font-title;
  font-size: 40px;
  font-weight: 700;
  color: $title;
  line-height: 49px;
  letter-spacing: -1px;
  text-transform: uppercase;
}

$max-width: 1280;

@mixin adapt-font ($desktopSize, $mobSize) {
  $newSize: $desktopSize - $mobSize;
  $newMobSize: $newSize + $newSize * 0.7;

  @media (max-width: $mobile-width) {
    //   font-size: calc(34px + 16 * (100vw / 1280));
    // font-size: calc(34px + 16*(100vw/1280));
    // font-size: calc(34px + 16*(100vw/1280px));
    font-size: calc(#{$mobSize + px} + #{$newMobSize} * ((100vw - 320px) / #{$max-width}));
  }

  // @media (min-width: $mobile-width) {
    font-size: calc(#{$mobSize + px} + #{$newSize} * (100vw / #{$max-width}));
  // }
}

