body {
  font-family: $font-main;
  font-size: 16px;
  line-height: 20.8px;
  font-weight: 500;
  color: $title;

  margin: 0;
  padding: 0;
}

html,
body {
  min-width: 320px;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a {
  text-decoration: none;
  color: $title;
  cursor: pointer;
}

img {
  max-width: 100%;
  height: auto;
}

.lock {
  overflow: hidden;
}

h2 {
  @include adapt-font(50, 30);
  line-height: 1.3em;
  letter-spacing: 1.5px;
  font-weight: normal;
}

