.intro {
  background-color: rgba(176, 140, 68, 0.7);
  background-image: url('../img/intro_cupcak.jpg');
  // background-image: url('../img/intro.jpg');

  background-size: cover;
  width: 100%;
  min-height: 90vh;
  // min-height: 450px;
  padding: 20px 0;
  background-attachment: fixed;
}

.intro__block {
  display: flex;
  align-items: center;
  height: 100%;
  min-height: 450px;
}

.intro__info {
  // height: 535px;
  width: 400px;
  background-color: rgba(253, 252, 250, 0.8);

  padding: 35px 35px;
  margin: 15px 0;
  display: flex;
  flex-direction: column;
  justify-content: center;

  & p {
    font-size: 1.3em;
    line-height: 1.3em;
    letter-spacing: 1.2px;
    margin-top: 0;
    margin-bottom: 20px;
  }

  & a.btn {
    width: 130px;
    // padding: 15px;
    // color: white;
    // background-color: $btn-brown;
    // border: none;
    // outline: none;
    // cursor: pointer;
    // border-radius: 10px;

    // font-size: 1em;
    // letter-spacing: 1.2px;

    // &:hover {
    //   opacity: 0.9;
    // }

    // &:active {
    //   opacity: 0.7;
    // }
  }
}

.intro__title {
  // font-size: 3em;
  // line-height: 1.3em;
  // letter-spacing: 1.5px;
  margin-top: 0;
  font-weight: normal;
}

.intro__info.animItems {
  transition: all 1s ease 0s;
  transform: translate(0, -120%);
  opacity: 0;
}

.intro__info.active {
  transform: translate(0, 0);
  opacity: 1;
  animation-delay: .5s;
}

.intro__info.fadeInDown {
  opacity: 0;
  animation: fadeInDown 1s ease both;
}

@keyframes fadeInDown {
  from {
    opacity: 0;
    transform: translate(0, -120%);
  }

  to {
    opacity: 1;
    transform: translate(0, 0);
  }
}

@media (max-width: $mobile-width) {
  .intro {
    // background-image: url('../img/intro-mobile2.jpg');
    background-image: url('../img/intro_cupcak.jpg');

    background-position: center;
    min-height: 55vh;
  }

  // .intro__title {
  //   font-size: 2.7em;
  // }

  .intro__info {
    width: 300px;
    padding: 35px 20px;

    & p {
      font-size: 1em;
      line-height: 1.3em;
    }

    & button {
      width: 130px;
      padding: 10px;

      font-size: 0.9em;
    }
  }


}
