.about {
  background-image: url('../img/blueberry.png');
  background-repeat: no-repeat;
  background-size: 10%;
  background-position-x: 0;
  background-position-y: 100%;
  background-color: #fdfdfd;
  // background-color: #fff1ed;

  // margin-bottom: 50px;
}

.about__block {
  display: flex;
  justify-content: space-between;
  flex-wrap: nowrap;
  padding: 90px 20px;
  // padding-top: 90px;
  align-items: flex-end;
}

.about__info {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;

  // outline: 1px solid red;

  width: 40%;
  // margin-top: 150px;

  & .about__title {
    // font-size: 3em;
    // line-height: 1.3em;
    // letter-spacing: 1.5px;
    margin-top: 0;
    // font-weight: normal;
    margin-bottom: 20px;
  }

  & p {
    // font-size: 1.3em;
    @include adapt-font(22, 14);
    line-height: 1.3em;
    letter-spacing: 1.2px;
  }
}

.about__photo {
  // display: flex;
  // align-items: flex-end;

  width: 55%;
  // max-height: 500px;
  // outline: 1px solid green;
}

.about__wrap {
  display: grid;


  // outline: 1px solid green;


    // display: grid;
    // grid-gap: 20px;
		// grid-template-columns: 45% 55%;
    // grid-template-rows: 37% 63%;

    grid-template-columns: repeat(8, 1fr);
    grid-template-rows: repeat(8, 1fr);
    grid-gap: 15px;

    & img {
      // display: block;
      width: 100%;
      height: 100%;
      object-fit: cover;

      // max-width: 100%;
      // height: auto;
    }

  & figure {
    margin: 0;
    // min-height: 300px;
    // outline: 1px solid red;
  }
}

.about__photo-first {
  grid-column: 1/5;
  grid-row: 1/4;
}

.about__photo-second {
  grid-column: 1/5;
  grid-row: 4/9;
}

.about__photo-third {
  grid-column: 5/9;
  grid-row: 1/9;
}

@media (max-width: $breakpoint) {
  .about {
    background-size: 20%;
    background-position-x: 90%;
    background-position-y: 95%;
    }


  .about__block {
    flex-direction: column;
    // padding: 20px;
    padding-top: 30px;

  }

  .about__info {
    width: 100%;
    margin-top: 40px;

  }

  .about__photo {
    // width: 100%;
    // height: 300px;
    display: none;
  }

  .about__wrap {
    height: 100%;
  }
}

