.contact {
  position: relative;
  background-color: #fdfdfd;
  min-height: 570px;

  &::after {
    content: '';
    background-image: url('../img/macarons-nobg.png');
    // background-size: 55%;
    background-repeat: no-repeat;
    // background-position: 100%  100px;

    position: absolute;
    bottom: 0;
    right: 0;
    width: 462px;
    height: 408px;
  }
}

.contact__block {
  padding-bottom: 70px;
}

// .contact__block.container {
//   margin: 0;
//   margin-right: auto;
//   padding: 50px;
// }

.contact__wrap {
  padding-bottom: 50px;
  padding-top: 50px;
  background-size: cover;
  width: 50%;
  max-width: 550px;
}

.contact__title {
  @include adapt-font(30, 25);
  letter-spacing: 1.5px;
  line-height: 1.3em;

  font-weight: 400;
  width: 100%;
}

.contact__form {
  display: flex;
  flex-direction: column;
  width: 100%;


  & input {
    margin-bottom: 20px;
    padding: 10px;
    border: none;
    background-color: #fff1ed;
    @include adapt-font(16, 14);
    cursor: pointer;

    &:hover {
      opacity: 0.8;
    }

    &:focus {
      outline: 1px solid #f8d0c4;
      opacity: 0.8;
    }
  }

  & input:focus::placeholder {
    color:transparent;
  }

}

@media (max-width: $breakpoint) {
  .contact {
    &::after {
      height: 377px;
    }
  }

  // .contact__block {
  //   margin: 0 auto;
  //   width: max-content;
  // }

  .contact__form {
    width: 100%;
  }
}


@media (max-width: $mobile-width) {
  .contact {
    &::after {
      content: none;
    }
  }

  .contact__form {
    width: auto;
    max-width: 490px;
    margin: 0 auto;
  }

  .contact__title {
    width: auto;
  }

  .contact__wrap {
    width: 100%;
    margin: 0 auto;
  }
}
