.assortment {
  background-color: #fdfdfd;

  // outline: 1px solid red;

  & .assortment__block {
    display: flex;
    flex-direction: column;

    padding-bottom: 40px;
  }
}


.assortment__btn {
  align-self: center;
}

.assortment__title {
  margin: 0;
  padding-bottom: 50px;
  padding-top: 50px;
}

.assortment__list {
  @include reset;
  display: flex;
  flex-wrap: wrap;
  // align-items: flex-end;

  // padding: 30px 0;

  // outline: 1px solid blue;

  & li:nth-child(3n) {
    margin-right: 0;
  }
}

.assortment__item {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  // outline: 1px solid green;

  width: 30%;
  margin-right: 5%;
  margin-bottom: 45px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
  border-radius: 3px;
}

.assortment__img {
  // margin-bottom: 40px;

  & img {
    display: block;
    width: 100%;
    object-fit: fill;
  }
}

@media (max-width: $mobile-width) {
  .assortment__list {

    & li:nth-child(3n) {
      margin-right: 5%;
    }

    & li:nth-child(2n) {
      margin-right: 0;
    }
  }

  .assortment__item {
    margin-bottom: 25px;
    width: 47.5%;
  }

  .item__title {
    // height: 15%;
    // flex-grow: 0;

    & p {
      font-size: 10px;
      margin: 5px 0;
    }

    & .item__name {
      font-size: 16px;
      margin-top: 10px;
    }
  }
}
