.footer {
    background: linear-gradient(to right, #fff1ed 75%, #f8d2d1 75%);
}

.footer__contacts {
  @include reset;
}

.footer__wrap {
  display: flex;
  flex-wrap: nowrap;
  justify-content: space-between;

  padding: 30px 25px;
}

.footer__logo {
  padding-top: 20px;
  width: 30%;

  & img {
    width: 80px;
  }
}

.footer__contacts {
  width: 40%;
}

.footer__contacts {
  @include adapt-font(18, 14);

  & a {
    position: relative;
    display: inline-block;
    padding: 0 10px;
    margin-bottom: 20px;
    color: #313131;
    font-style: italic;

    &:hover,
    :focus {
      border-bottom: 1px solid $btn-brown;
      color: $btn-brown;
    }

    & :active {
      opacity: 0.8;
    }
  }

  & svg {
    position: absolute;
    top: 0;
    left: -20px;
  }
}

.footer__social {
  width: 15%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
}

@media (max-width: $breakpoint) {
  .footer__logo {
    width: 15%;
  }

  .footer__social {
    width: 25%;
  }
}

@media (max-width: $mobile-width) {
  .footer__logo {
    display: none;
  }

  .footer__wrap {
    flex-direction: column;
    justify-content: space-evenly;
  }

  .footer__contacts {
    padding: 0 25px;
    min-width: 230px;


  }

  .footer__social {
    width: 100%;
    max-width: 200px;
  }
}
