.social {
  @include reset;
}

.social__vk {
  width: 37px;
  height: 37px;
}

.social__inst {
  width: 25px;
  height: 25px;
}

.social__telegram {
  width: 30px;
  height: 30px;
}

@media (max-width: $mobile-width) {
  .social__vk {
    width: 30px;
    height: 30px;
  }

  .social__inst {
    width: 20px;
    height: 20px;
  }

  .social__telegram {
    width: 25px;
    height: 25px;
  }

  .social {
    margin-top: 30px;
  }
}
