.item__link {
  // height: 90%;
  flex-grow: 1;
}

.item__img {
  position: relative;
  width: 100%;
  height: 100%;
  cursor: pointer;
  // height: 80%;
  // outline: 1px solid green;
  transition: opacity 0.2s ease;
    // & a {
  //   display: block;
  //   height: 100%;
  // }

  & img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;

    border-radius: 3px;
  }
}


.item__title {
  // height: 15%;
  flex-grow: 0;

  & p {
    font-size: 16px;
    text-align: center;
  }

  & .item__name {
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 0;
  }
}

